@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;600;700;800&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --paragraph-18-pixel: Nunito;

  /* font sizes */
  --font-size-base: 16px;
  --paragraph-18-pixel-size: 18px;
  --heading-01-56-pixel-size: 56px;

  /* Colors */
  --color-white: #fff;
  --primary-color: #ff007a;

  /* Gaps */
  --gap-21xl: 40px;

  /* Paddings */
  --padding-base: 16px;
  --padding-9xl: 28px;

  /* border radiuses */
  --br-13xl: 32px;

  /* Effects */
  --shadow-xs: 0px 1px 2px rgba(16, 24, 40, 0.05);
}
